import { _decorator, Component } from 'cc';
const { ccclass } = _decorator;

@ccclass('SingletonComponent')
export class SingletonComponent<T extends Component> extends Component {
    private static _instanceMap: Map<Function, Component> = new Map();

    public static getInstance<T extends Component>(this: { new(): T }): T {
        if (!SingletonComponent._instanceMap.has(this)) {
            const instance = new this();
            SingletonComponent._instanceMap.set(this, instance);
        }
        return SingletonComponent._instanceMap.get(this) as T;
    }
}
